<template>
  <div class="title">
    <div class="jian">{{title}}</div>
    <div class="jiantou" v-if="show"  @click="djs" ></div>
  </div>
</template>
<script>
export default {
  props: {
    title: {
      type: String
    },
    show: {
      type: Boolean,
      default: true
    },
    method:{
      type:Boolean,
      default:true
    }
  },
  // watch:{
  //   method(e){
  //    this.a=e
  //   }
  // },
  // data(){
  //   return{
  //     a:''
  //   }
  // },
  methods:{
    djs(){
     if(this.method){
       this.$router.go(-1);
    //  console.log(this.dj)
     }else{
       //父组件自定义方法
       this.$emit('leftBtn')
     }
    }
  }
};
</script>
<style lang="less" scoped>
.title {
    position: fixed;
    top: 0;
    background: #fff;
  font-weight: 700;
  text-align: center;
  z-index: 999;
  color: #2e3133;
  padding: 0;
  height: 1.1736rem;
  width: 100%;
  line-height: 1.173333rem;
  .jian {
    font-weight: 700;
    text-align: center;
    font-size: 0.48rem;
    // font-weight: 400;
    color: #2e3133;
    line-height: 1.173333rem;
    margin: 0 2.346667rem;
    height: 1.066667rem;
    width: auto;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .jiantou {
    position: absolute;
    width: 0.8rem;
    height: 0.8rem;
    top: 0.133333rem;
    left: 0.333333rem;
  }
  .jiantou::before {
    border-width: 0.053333rem 0 0 0.053333rem;
    content: "";
    border: 0.026667rem solid #4d4f50;
    border-width: 0.053333rem 0 0 0.053333rem;
    position: absolute;
    width: 0.32rem;
    height: 0.32rem;
    // border: 0.026667rem solid #ccc;
    border: 0.026667rem solid #4d4f50;
    border-width: 0.065667rem 0 0 0.065667rem;
    -webkit-transform: rotate(315deg);
    transform: rotate(315deg);
    top: 0.213333rem;
    left: 0.186667rem;
  }
}
</style>


